.hero {
  display: flex;
}

.hero__text {
  width: 42%;
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.hero__line span {
  display: block;
  width: 38px;
  height: 3px;
  background: #363636;
  margin-top: 60px;
  margin-bottom: 1rem;
}

.hero__title h2 {
  font-weight: 700;
  color: #363636b3;
  font-size: 18px;
  font-family: "Roboto Slab";
  font-weight: 700;
  line-height: 133%;
  padding-right: 10px;
}

.hero__button button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 40px;
  background-color: var(--primary-red);
  padding: 12px 30px;
  text-transform: uppercase;
  color: #fff;
  font-family: "Roboto Slab";
  font-weight: 400;
  font-size: 18px;
  line-height: 1.3rem;
  cursor: pointer;
}

.hero__button button:hover {
  background-color: #8b0923;
}

.hero__description p {
  font-size: 10px;
  font-family: "Roboto Slab";
  font-weight: 400;
  color: #363636b3;
  max-width: 285px;
}

.hero__image {
  background-image: url(../../assets/hero.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50%;
  width: 58%;
  height: 555px;
}

@media (max-width: 992px) {
  .hero {
    flex-direction: column-reverse;
  }

  .hero__text {
    width: 100%;
  }

  .hero__image {
    width: 100%;
    height: 250px;
    background-size: cover;
    background-position: center 2px; 
  }

  .hero__title {
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .hero__button {
    margin-bottom: 20px;
  }

  .hero__button button {
    width: 100%;
  }

  .hero__description p {
    max-width: none;
  }
}

@media (max-width: 768px) {
  .hero__image {
    background-size: cover;
    background-position: center;
  }
}

@media (max-width: 576px) {
  .hero__text {
    padding: 0;
  }

  .hero__line span {
    margin-top: 40px;
  }

  .hero__title h2 {
    font-size: 1rem;
  }

  .hero__image {
    height: 160px;
  }
}
