.scroll {
  position: fixed;
  border-radius: 50%;
  background-color: var(--primary-red);
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  right: 100px;
  bottom: 50px;
}

.scroll::before {
  content: '';
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.29289 5.29289C8.68342 4.90237 9.31658 4.90237 9.70711 5.29289L15.7071 11.2929C16.0976 11.6834 16.0976 12.3166 15.7071 12.7071L9.70711 18.7071C9.31658 19.0976 8.68342 19.0976 8.29289 18.7071C7.90237 18.3166 7.90237 17.6834 8.29289 17.2929L13.5858 12L8.29289 6.70711C7.90237 6.31658 7.90237 5.68342 8.29289 5.29289Z' fill='%23FFFFFF'/%3e%3c/svg%3e ");
  background-repeat: no-repeat;
  background-size: cover;
  width: 30px;
  height: 30px;
  transform: rotate(-90deg);
}

@media (max-width: 576px) {
  .scroll {
    width: 40px;
    height: 40px;
    right: 10px;
    bottom: 30px;
  }
}
